/* eslint-disable jsx-a11y/label-has-associated-control */

import { Component, Element, h, Host, Listen, Prop, State } from '@stencil/core';
import { Dealer } from '../../entities/content';
import { showDealerSearchDropdown } from '../../utility/helpers/dealer-search-helper';
import { splitLocale } from '../../entities/locale';
import { navContentStore } from '../../state/nav-content-store';

type DealerSearchFormStrategy = 'geolocation' | 'dropdown' | 'byNumberOfDealers';

@Component({
  tag: 'phn-dealer-search-form',
  styleUrl: 'dealer-search-form.scss',
  scoped: true,
  assetsDirs: ['./assets']
})
export class DealerSearchForm {
  @Prop() dealer!: Dealer;
  @Prop() locale!: string;
  @Prop() showHeadline = true;
  @Prop() breakpoint!: number;

  @State() showOverlay = false;
  @State() formErrorMessage: string | null = null;
  @State() showSkeleton = true;

  @Listen('predictionsToggle')
  toggleOverlay(e: CustomEvent<boolean>): void {
    this.showOverlay = e.detail;
  }

  @State() animateResults = false;

  @Element() el!: HTMLPhnDealerSearchFormElement;

  @Listen('removeSkeletonOnDealerSearchForm')
  loadedAutoCompleteListener(_event: Event): void {
    this.showSkeleton = false;
  }

  private getDealerFormStrategy(): DealerSearchFormStrategy {
    const { dealer } = navContentStore.state;
    if (dealer.numberOfDealers <= 5) {
      return 'byNumberOfDealers';
    }

    if (showDealerSearchDropdown(splitLocale(this.locale).country)) return 'dropdown';

    return 'geolocation';
  }

  private formComponent(): HTMLElement {
    const dealerSearchFormStrategies: Record<DealerSearchFormStrategy, () => HTMLElement> = {
      geolocation: () => <phn-dealer-search-form-geolocation locale={this.locale} />,
      dropdown: () => <phn-dealer-search-form-dropdown locale={this.locale} breakpoint={this.breakpoint} />,
      byNumberOfDealers: () => <phn-dealer-search-by-number-of-dealers locale={this.locale} />
    };

    const formStrategy = this.getDealerFormStrategy();
    return dealerSearchFormStrategies[formStrategy]();
  }

  render(): HTMLPhnDealerSearchFormElement {
    return (
      <Host class={this.showSkeleton ? 'skeleton' : ''}>
        {this.formComponent()}

        <phn-dealer-search-results locale={this.locale} breakpoint={this.breakpoint} showOverlay={this.showOverlay} />
      </Host>
    );
  }
}
