@import '../../scss';

:host {
  display: block;

  &.skeleton {
    pointer-events: none;
    user-select: none;

    & .skeleton-headline {
      width: auto;
      height: p-px-to-rem(50px);
      background: $pds-theme-light-contrast-low;
      margin-bottom: p-px-to-rem(28px);
      border-radius: 4px;
    }
  }

  phn-headline {
    margin-bottom: p-px-to-rem(36px);
  }
}
